@import url(../../node_modules/continuo/dist/css/continuo.css);
dialog {
  position: absolute;
  left: 0;
  right: 0;
  width: -moz-fit-content;
  width: -webkit-fit-content;
  width: fit-content;
  height: -moz-fit-content;
  height: -webkit-fit-content;
  height: fit-content;
  margin: auto;
  border: solid;
  padding: 1em;
  background: white;
  color: black;
  display: block; }

dialog:not([open]) {
  display: none; }

dialog + .backdrop {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.1); }

._dialog_overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0; }

dialog.fixed {
  position: fixed;
  top: 50%;
  -webkit-transform: translate(0, -50%);
  transform: translate(0, -50%); }

.mdl-navigation .mdl-button {
  margin-left: .5em; }

.mdl-layout__header-row {
  padding: 0 25px 0 25px !important; }

.score_header {
  min-height: 45px !important;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1); }
  .score_header .mdl-layout__header-row {
    height: 45px;
    padding: 0 5px 0 5px; }
  .score_header .mdl-layout-title {
    margin: 0 0 0 8px; }

.score {
  padding: 5px;
  overflow: hidden;
  min-height: 400px; }

.score_collapsed {
  height: 45px !important;
  overflow: hidden; }
  .score_collapsed .score {
    display: none; }
  .score_collapsed .score_pagination {
    display: none; }

.cnt-container {
  position: relative; }

.cnt-emabox {
  position: absolute !important;
  margin-left: 35px !important; }
  .cnt-emabox .cnt-emabox-hide {
    position: absolute;
    left: -20px;
    top: 30px;
    font-size: 17px;
    background-color: #e0e0e0;
    border-radius: 20px;
    padding: 2px 8px 2px 8px; }

/* Make sure to make responsive */
.score_assertion_dialog, .score_relationship_dialog, .score_assertions_dialog {
  width: 500px !important; }
  .score_assertion_dialog .types, .score_relationship_dialog .types, .score_assertions_dialog .types {
    padding: 4px; }
  .score_assertion_dialog label, .score_relationship_dialog label, .score_assertions_dialog label {
    width: auto; }
  .score_assertion_dialog .drop, .score_relationship_dialog .drop, .score_assertions_dialog .drop {
    float: right;
    margin-top: -4px; }
  .score_assertion_dialog .score_preview_cnt, .score_relationship_dialog .score_preview_cnt, .score_assertions_dialog .score_preview_cnt {
    position: absolute;
    background: white;
    top: 10px;
    right: 10px;
    padding: 5px; }
  .score_assertion_dialog .score_preview_cnt2, .score_relationship_dialog .score_preview_cnt2, .score_assertions_dialog .score_preview_cnt2 {
    position: absolute;
    background: white;
    top: 50px;
    right: 50px;
    padding: 5px; }
  .score_assertion_dialog .score_preview_close, .score_relationship_dialog .score_preview_close, .score_assertions_dialog .score_preview_close {
    position: absolute !important;
    top: 0;
    right: 0;
    cursor: pointer; }
  .score_assertion_dialog i.clickable, .score_relationship_dialog i.clickable, .score_assertions_dialog i.clickable {
    cursor: pointer; }

svg .preview_selected {
  color: #ffb944;
  fill: currentColor; }

.mdl-button--attention {
  background-color: #ffb944 !important; }

.truncate {
  width: 160px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis; }

.truncate_short {
  width: 112px; }

.assert_ema, .assert_score {
  text-align: right;
  font-size: 20px;
  font-weight: bold;
  width: 450px;
  white-space: nowrap;
  overflow: hidden;
  height: 30px;
  text-overflow: ellipsis; }

.assert_ema {
  color: #448aff; }

.hide_button {
  position: absolute !important;
  top: 10px;
  right: 10px; }

.hide_mode {
  position: absolute !important;
  z-index: 99;
  left: 50%; }

.mask {
  position: absolute;
  z-index: 1001;
  background-color: lightgrey;
  opacity: 0.4; }

.dialog_select {
  margin: 1em 0 1em 0;
  border-top: none;
  border-left: none;
  border-right: none;
  background-color: transparent;
  border-bottom: 3px solid #607d8b;
  min-width: 180px;
  color: rgba(0, 0, 0, 0.54);
  font-size: 16px;
  outline: none; }

.dialog_select:disabled {
  color: rgba(0, 0, 0, 0.26); }

.select_label {
  color: #607d8b;
  font-size: 12px;
  top: 4px; }

.messages {
  display: none;
  background-color: #ffb944 !important;
  color: black;
  padding: 5px; }

.rel_item {
  padding: 0 !important; }

.import {
  width: 500px !important; }
  .import .mdl-tabs__panel {
    padding-top: 20px;
    text-align: center; }

.addFile {
  width: 500px !important; }
  .addFile .mdl-tabs__panel {
    padding-top: 20px; }
  .addFile label {
    height: 100% !important; }

.mdl-button--file input {
  cursor: pointer;
  height: 100%;
  right: 0;
  opacity: 0;
  position: absolute;
  top: 0;
  width: 300px;
  z-index: 4; }

.mdl-textfield--file .mdl-textfield__input {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  width: calc(100% - 32px); }

.mdl-textfield--file .mdl-button--file {
  right: 0; }

.direction {
  margin-bottom: 50px; }
  .direction label {
    display: block;
    margin-bottom: 10px; }

#loader {
  display: none;
  margin: 20px 0 0 24px; }
  #loader img {
    width: 50px;
    height: auto; }
